import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CadastroComponent } from './cadastro/cadastro.component';
import { ConsultaComponent } from './consulta/consulta.component';
import { SobreComponent } from './sobre/sobre.component';

//Aqui  definido as rotas da aplicao - Requisito da Atividade #09
const routes: Routes = [
  {
    //Aqui  definido o caminho para o componente CadastroComponent
    //O caminho  definido como cadastro/:modo, onde :modo  um parmetro
    //identificando se estamos em um novo cadastro ou em edio
    path: 'cadastro/:modo',
    component: CadastroComponent    
  },
  //rota para o componente ConsultaComponent
  { path: 'consulta', component: ConsultaComponent },
  //rota para o componente SobreComponent
  { path: 'sobre', component: SobreComponent },
  //rota padro, caso o usurio digite um caminho invlido, ser redirecionado para a pgina de cadastro
  { path: '', redirectTo: '/cadastro', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
